<template>
  <div class="toolbar-container">
    <div :class="tabContainerClasses">
      <ul>
        <li
          v-for="(item, index) in menuItems"
          :key="index"
          v-bind:class="{ 'is-active': item.link === appPage }"
        >
          <a v-on:click="movePage(item.link)">
            <span class="gn-icon is-small">
              <i :class="['fa', item.class]"></i>
            </span>
            <span>{{ item.title }}</span>
          </a>
        </li>
      </ul>
    </div>
    <div class="gn-select is-medium">
      <select v-model="selectedTheme" @change="loadSelectedTheme">
        <option v-for="theme in color_themes" :key="theme" :value="theme">{{ theme }}</option>
      </select>
    </div>
  </div>
</template>

<script>
  import 'gnui/dist/styles/default.css'
  import router from '@/router'
  import store from '@/store'
  import { mapGetters } from 'vuex'

  const COLOR_THEMES = ['default', 'nac', 'insights', 'gpi', 'ztnac', 'green24']

  export default {
    name: 'Toolbar',
    metaInfo() {
      return {
        title: store.getters.appTitle,
        meta: [],
        link: []
      }
    },
    components: {},
    directives: {},
    data() {
      const cashedTheme = localStorage.getItem('selectedTheme')

      return {
        selectedTheme: cashedTheme || COLOR_THEMES[0]
      }
    },
    computed: {
      ...mapGetters(['appTitle', 'appPage']),
      menuItems() {
        return [
          {
            title: this.$t('menuItems.HOME'),
            link: 'home',
            class: 'fa-home'
          },
          // {
          // 	title: this.$t('menuItems.CONF_ENGINE'),
          // 	link: 'confEngine',
          // 	class: 'fa-cog'
          // },
          {
            title: this.$t('menuItems.CONF_TYPES'),
            link: 'confTypes',
            class: 'fa-list'
          }
        ]
      },
      color_themes() {
        return COLOR_THEMES
      },
      tabContainerClasses() {
        return {
          'gn-tab tab-container': true,
          'is-border-bottom':
            this.selectedTheme === COLOR_THEMES[4] || this.selectedTheme === COLOR_THEMES[5]
        }
      }
    },
    methods: {
      movePage: link => {
        store.dispatch('setPage', link)
        router.push({ name: link }, () => {})
      },
      loadSelectedTheme() {
        const stylePath = `./css/themes/${this.selectedTheme}.css`

        const oldStyle = document.getElementById('dynamic-style')
        if (oldStyle) {
          oldStyle.parentNode.removeChild(oldStyle)
        }

        const styleLink = document.createElement('link')
        styleLink.id = 'dynamic-style'
        styleLink.rel = 'stylesheet'
        styleLink.href = stylePath
        document.head.appendChild(styleLink)

        this.selectedTheme && localStorage.setItem('selectedTheme', this.selectedTheme)
      }
    },
    mounted() {
      if (this.selectedTheme !== COLOR_THEMES[0]) {
        this.loadSelectedTheme()
      }
    }
  }
</script>

<style lang="scss">
  .toolbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 7px;
    height: 40px;
  }
  .tab-container {
    position: relative;
    top: 12px;
    &.is-border-bottom {
      top: 7px;
    }
  }
</style>
