import * as types from '@/store/types'
import i18n from '@/plugins/i18n'

// getters
const getters = {
  locale: state => state.locale
}

// actions
const actions = {
  setLocale({ commit }, locale) {
    commit(types.SUCCESS, null)
    commit(types.ERROR, null)
    i18n.locale = locale
    window.localStorage.setItem('locale', JSON.stringify(locale))
    commit(types.SET_LOCALE, locale)
  }
}

// mutations
const mutations = {
  [types.SET_LOCALE](state, value) {
    state.locale = value
  }
}

// state
const state = {
  locale: JSON.parse(localStorage.getItem('locale')) || process.env.VUE_APP_SYSTEM_LOCALE || 'ko'
}

export default {
  state,
  getters,
  actions,
  mutations
}
