import * as types from '@/store/types'
import packageJson from '../../../package.json'

// getters
const getters = {
  appTitle: state => state.appTitle,
  appVersion: state => state.appVersion,
  appPage: state => state.appPage,
  appCopy: state => state.appCopy,
  appAuthor: state => state.appAuthor
}

// actions
const actions = {
  setAppVersion({ commit }, payload) {
    commit(types.SET_APP_VERSION, payload)
  },
  setPage({ commit }, page) {
    commit(types.SET_APP_PAGE, page)
  },
  // eslint-disable-next-line no-unused-vars
  isActive({ commit }, page) {
    return this.getters.appPage === page
  }
}

// mutations
const mutations = {
  // set app version
  [types.SET_APP_VERSION](state, version) {
    state.appVersion = version
  },
  // set page
  [types.SET_APP_PAGE](state, page) {
    state.appPage = page
  }
}

// state
const state = {
  appTitle: packageJson.name,
  appVersion: packageJson.version,
  appCopy: packageJson.copy,
  appAuthor: packageJson.author,
  appPage: 'home'
}

export default {
  state,
  getters,
  actions,
  mutations
}
