import * as types from '@/store/types'

// getters
const getters = {
  showLoading: state => state.showLoading
}

// mutations
const mutations = {
  [types.SHOW_LOADING](state, value) {
    state.showLoading = value
  }
}

// state
const state = {
  showLoading: false
}

export default {
  state,
  getters,
  mutations
}
