import Vue from 'vue'
import i18n from '@/plugins/i18n'
import app from '@/App.vue'
import router from '@/router/index'
import store from '@/store/index'
// fontawesome
import '@fortawesome/fontawesome-free/css/all.min.css'
// gnui conf engine
import '../../dist/styles/gnConfEngine.css'
import GnConfEngine from '../../dist/js/gnConfEngine.esm'

Vue.config.productionTip = false

new Vue({
  router,
  store,
  i18n,
  render: h => h(app),
  created() {
    // set locale
    store.dispatch('setLocale', store.getters.locale)
    // set GnConfEngine global settings
    GnConfEngine.setDefaultLocal('ko')
    GnConfEngine.setSupportLang(['en', 'ko'])
    GnConfEngine.setBaseUrl([process.env.VUE_APP_API_URL || '/mc/', 'api/'].join(''))
  }
}).$mount('#app')
