import * as types from '@/store/types'

// getters
const getters = {
  activeMenu: state => state.activeMenu,
  confCategories: state => state.confCategories
}

// actions
const actions = {
  activeMenu({ commit }, menu) {
    commit(types.SET_CONF_ENGINE_ACTIVE_MENU, menu)
  }
}

// mutations
const mutations = {
  // set active menu
  [types.SET_CONF_ENGINE_ACTIVE_MENU](state, menu) {
    state.activeMenu = menu
  }
}

// state
const state = {
  confCategories: [
    { label: 'GN_CONF_ENGINE_TMP', value: '@@@GN_CONF_ENGINE_TMP' },
    { label: 'WebUIGlobal', value: 'WebUIGlobal' },
    { label: 'ChartConverter', value: 'ChartConverter' },
    { label: '@ADVANCE', value: '@ADVANCE' },
    { label: '@abv', value: '@abc' }
  ],
  activeMenu: null
}

export default {
  state,
  getters,
  actions,
  mutations
}
